<template>
  <div>

  </div>
</template>

<script>
import Bus from "../../../utils/Bus";
export default {
name: "CommunityBuilderSetInfo",
  data(){
     return{
       communityBuilderInfo:{},
     }
  },
  destroyed(){
    Bus.$off('globalMsgForCommunityBuilder');
    console.info("进入CommunityBuilderSetInfo destroyed方法");
  },
  created() {
    var that = this;
    console.info("进入CommunityBuilderSetInfo created方法");
    Bus.$on('globalMsgForCommunityBuilder',function(val){//监听各组件传递的 globalMsg 事件
      console.log("监听到的消息："+JSON.stringify(val));
      that.communityBuilderInfo = val;

    });
  },
  methods:{
    showPublicInfo:function (){
      console.info("this.communityBuilderInfo:"+JSON.stringify(this.communityBuilderInfo));
      return this.communityBuilderInfo;

    }
  }

}
</script>

<style scoped>

</style>



