  mounted() {
    // let that = this;
    // window.onresize= function temp() {
    //   that.clientHeight = (window.innerHeight-105);
    // };

  },
  created() {


  },

  directives: {

    trigger:{

      inserted(el, binging){
        console.log("inserted:"+el.id);
        if(el.id==='ballBtn'){
           el.click();
        }

      }
    },
    drag(el) {
      let oDiv = el;  // 当前元素
      let firstTime = "", lastTime = "";  //声明时间用来区分拖拽和单机
      document.onselectstart = function () {       // 禁止选择网页上的文字
        return false;
      }
      oDiv.onmousedown = function (e) {
        let disX = e.clientX - oDiv.offsetLeft          // 鼠标按下，计算当前元素距离可视区的距离    // 算出鼠标相对元素的位置     // 用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
        let disY = e.clientY - oDiv.offsetTop
        document.getElementById("dragbtn").setAttribute("data-flag", false);
        document.getElementById("dragbtn").setAttribute("show2", oDiv.offsetLeft);
        // console.log(e.clientX +"鼠标横轴")
        // console.log(e.clientY+"鼠标Y轴")
        // console.log(oDiv.offsetLeft+"元素的距离左侧距离")
        // console.log(oDiv.offsetTop)
        // console.log(disX+"元素本身的宽度") //元素本身的宽度
        // console.log("本身的宽高",oDiv.clientWidth);//本身的宽度
        firstTime = new Date().getTime();
        document.onmousemove = function (e) {


          let l = e.clientX - disX               // 用鼠标的位置减去鼠标相对元素的位置，得到元素的位置 左侧多少 ，不是定型尺寸哦 ，是定位尺寸
          let t = e.clientY - disY
          // console.log(e.clientX+"移动后的鼠标位置 ，已经和这个没移动前不一样了  ")
          if (l <= 0) {
            l = 0;
          } else if (l >= document.documentElement.clientWidth - oDiv.clientWidth) {
            //document.documentElement.clientWidth 屏幕的可视宽度
            l = document.documentElement.clientWidth - oDiv.clientWidth
          }
          document.getElementById("dragbtn").setAttribute("show2", l.toString()); //无法使用this，只好赋值给属性
          if (t <= 0) {
            t = 0;
          } else if (t >= document.documentElement.clientHeight - oDiv.clientHeight) {  //本身的宽高  oDiv.clientHeight
            t = document.documentElement.clientHeight - oDiv.clientHeight              // document.documentElement.clientHeight 屏幕的可视高度
          }
          oDiv.style.left = l + 'px'             // 移动当前元素
          oDiv.style.top = t + 'px'
        }
        document.onmouseup = function (e) {

          document.onmousemove = null;
          document.onmouseup = null;
          lastTime = new Date().getTime();
          if ((lastTime - firstTime) < 200) {
            document.getElementById("dragbtn").setAttribute("data-flag", true)
          }
        }
        return false          // return false不加的话可能导致黏连，就是拖到一个地方时div粘在鼠标上不下来，相当于onmouseup失效
      }
    }
  },

  methods:{
    //检测按钮权限
    checkBtnPermission(id){
      // let bRe = false;
      let bRe = true;
      for(let k of Object.keys(this.btnPermission)){
        if(id===k && this.btnPermission[k]===false){
          bRe = false;
          break;
        }
      }
      // for(var i1=0;i1<this.btnPermission.length;i1++){
      //   if(id===this.btnPermission[i1]){
      //     bRe = true;
      //     break;
      //   }
      // }
      return bRe;
    },
    deleteData(){
      let vIds=[];
      for (let i = 0; i <this.multipleSelection.length ; i++) {
        vIds[i]=this.multipleSelection[i].vci_uuid;
      }
      console.info("要删除的ID："+vIds);
    },
    getRowKeys(row){
      return row.vci_uuid;
    },
    showCb(){
      // console.info("返回对象："+JSON.stringify(  this.$refs.cb.showPublicInfo() ));
      this.$emit("parent_method","");
    },
    confCardBodyHeigth(){

      if(this.cardBodyHeight===false){
        this.cardBodyHeight = true;
        this.cardBodyStyle = 'padding-top:10px;padding-bottom:10px;';
        this.icon1='el-icon-caret-top';
      }else{
        this.cardBodyHeight = false;
        this.cardBodyStyle = 'padding-top:0px;padding-bottom:0px;';
        this.icon1='el-icon-caret-bottom';
      }
      console.info("confCardBodyHeigth:"+this.cardBodyHeight);
    },
    colCheckCommand(){
      console.info("colsChecked："+this.colsChecked);
      this.colsMap.filter(i => {
        console.info("遍历"+i.value);
        if (this.colsChecked.indexOf(i.value) !== -1 ) {
          console.info("选中集合中存在" + i.value);
          i.isShow = true

        } else {
          i.isShow = false

        }
      })

      console.info('colsList:'+JSON.stringify(this.colsMap));
    },
    reQueryValveCurrentData (){
      this.queryParam.currentPage = 1;
      this.queryValveCurrentData ();
    },
    queryValveCurrentData (){
      console.info("组织机构ID："+this.treeNodeInfo.treeNodeOrgId);
      let that = this;
      //console.info("form元素："+JSON.stringify( this.ruleForm ));
      let _jsondata_ =   {

          perPageSize:this.queryParam.pageSize,
          currentPage:this.queryParam.currentPage,
          vi_setup_position:this.ruleForm.vi_setup_position,
          valve_currentdata_backwater_1:this.ruleForm.valve_currentdata_backwater_1,
          valve_currentdata_backwater_2:this.ruleForm.valve_currentdata_backwater_2,
          valve_openvalue_1:this.ruleForm.valve_openvalue_1,
          valve_openvalue_2:this.ruleForm.valve_openvalue_2,


      } ;
      if(this.sortOrderCol!==''){
        _jsondata_.orderby = this.sortOrderCol;
      }
      //console.info("json:"+JSON.stringify(_jsondata_));

      const loading = this.$loading({
        lock: true,
        text: '查询中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });

      var p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.postQueryValveCurrentDataList,
                //this.customGlobal.serverUrl.baseURI,
                this.customGlobal.serverUrl.orgBaseURI,
                 {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
        );

      p1.then((res) => {
        console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){

          loading.close();
          that.tableData = res.data.rows;
          that.queryParam.totalRow = res.data.total;

        }else{
          console.info("查询失败");
          loading.close();

          that.tableData = [];
          this.$alert(res.data.msg, '查询失败', {
            confirmButtonText: '确定',
            callback: action => {
              console.info('data:'+JSON.stringify(res.data));
                if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                  this.$router.replace({path:'/login'});
                }
            }
          });
        }

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        that.tableData = [];
        this.$alert(JSON.stringify(error.message), '查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        that.tableData = [];
        this.$alert(JSON.stringify(res.message), '查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });

    },
    sortColumn(column) {
      console.log("已经排序项：" + JSON.stringify(column));
      let orderFlag = '';
      if (column.order != null) {
        if (column.order === "descending") {
          orderFlag = 'desc';
        } else if (column.order === "ascending") {
          orderFlag = 'asc';
        }
        console.info("属性名称："+ column.prop+",排序方式："+column.order);

        this.sortOrderCol = "vci." + column.prop + " " + orderFlag;
        console.info("用户选择排序列："+this.sortOrderCol);
        this.queryValveCurrentData ();
      }else{
        this.sortOrderCol = '';
      }

    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.queryParam.pageSize = val;
      this.queryValveCurrentData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.queryParam.currentPage = val ;
      this.queryValveCurrentData();
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.info("选择的行："+JSON.stringify( this.multipleSelection) );
    },


    //打开悬浮球
    openBox() {


      let isDrag = document.getElementById("dragbtn").getAttribute("data-flag");
      if(isDrag==true){
        return;
      }
      // if (isClick !== 'true') {
      //   console.log('拖拽事件')
      //   return false;
      // } else {

      if(this.ballIcon==='el-icon-more'){
        this.ballIcon='el-icon-right';
      }else{
        this.ballIcon='el-icon-more';
      }

        var x = this.$refs['ballBtn'].$el.getBoundingClientRect().x;
        var w =  document.documentElement.clientWidth;
          //console.log('点击事件x:'+x+",w:"+w/2);
          this.ballShow = !this.ballShow;

          if(x>(w/2)){//大于窗口一半
            document.getElementById("buttonMenu").style.left = this.menuLeft + 'px';
          }else{//小于窗口一半
            document.getElementById("buttonMenu").style.left = Math.abs( this.menuLeft2 )  + 'px';// '70px';
          }


        /**
         * 使用说明 就是 这个时候的 show2 是一个属性 元素里面的属性 用 v-show
         * 70 和 -130 是偏移的距离  根据展示菜单的 大小来设定  展示菜单  的 style 就是这个 left  和width  见 上面  内联样式 width: 145
         * 使用的原理是 菜单长度大于 悬浮距离左侧长度 则向右偏移
         * 750是为了大于屏幕的右侧就行，因为初始化时 拖动还没有0 ，这个 和拖动到0  重复 的
         */
        // let dd33 = document.getElementById("dragbtn").getAttribute("show2"); //用于初始化时也能向右展开
        // let dd = document.getElementById("dragbtn").getAttribute("show2");//拖拽后悬浮纽长度
        // let dd2 = document.getElementById("buttonMenu").style.width; //菜单的长度
        // let menuLength = dd2.substring(0, dd2.lastIndexOf("px"));
        // if (Number(dd) < Number(menuLength)) {
        //   if (Number(dd33) > 750 && Number(dd) === 0) {
        //   } else {
        //     //向右展开
        //     document.getElementById("buttonMenu").style.left = this.floatDivWidth + 'px';
        //     document.getElementById("buttonMenu").style.display = 'flex';
        //     document.getElementById("buttonMenu").style.justifyContent = 'space-between';
        //   }
        // } else {
        //   //向左展开
        //   document.getElementById("buttonMenu").style.left = this.menuLeft + 'px';
        //   document.getElementById("buttonMenu").style.display = 'flex';
        //   document.getElementById("buttonMenu").style.justifyContent = 'space-between';
        // }
      // }
    },

  }
}
</script>

