<template>
  <!--截图功能-->
  <div class="el-cropper" style="margin-bottom: 20px;">
    <div>
      <div style="margin: 10px 0">
        <div style="font-weight: 600">操作说明：</div>
        <div>通过鼠标滚轮调整图片大小</div>
        <div>通过鼠标左键调整裁剪框位置</div>
      </div>
      <div class="cropper-content">
        <div class="cropper">
          <vueCropper ref="cropper"
                      :img="options.img"
                      :outputSize="options.outputSize"
                      :outputType="options.outputType"
                      :full="options.full"
                      :canMove="options.canMove"
                      :canMoveBox="options.canMoveBox"
                      :original="options.original"
                      :autoCrop="options.autoCrop"
                      :autoCropWidth="options.autoCropWidth"
                      :autoCropHeight="options.autoCropHeight"
                      :fixedBox="options.fixedBox"
                      :enlarge="options.enlarge"
                      :centerBox="options.centerBox"
                      :fixedNumber="options.fixedNumber"
                      :fixed="options.fixed"
                      :maxImgSize="options.maxImgSize"
                      :mode="options.mode"
                      :infoTrue="options.infoTrue"
                      :high="options.high"
                      @realTime="realTime"
          ></vueCropper>
        </div>
        <div class="show-preview" :style="{'overflow': 'hidden'}" v-show="false">
          <div class="wait">预览区域：</div>
          <div class="preview">
            <img :src="imgsrc" alt="" :style="scaleFun">
          </div>
        </div>
      </div>
    </div>
    <div style="  margin-top: 10px;">
      <el-row>
        <!-- 选择图片 -->
        <el-col :span="6">
          <div>
            <label class="el-button el-button--default" for="uploads1">选择图片
              <input type="file" id="uploads1"  ref="reference" style="position:absolute; clip:rect(0 0 0 0);"
                      accept="image/png, image/jpeg, image/jpg" @change="uploadImg($event, 1)"/>
            </label>
          </div>
        </el-col>
        <!-- 功能按钮 -->
        <el-col :span="6">
          <div>
            <el-button @click="rotateLeft"><span style="font-weight: 600;">↺</span></el-button>
            <el-button @click="rotateRight"><span style="font-weight: 600;">↻</span></el-button>
          </div>
        </el-col>
        <el-col :span="12">
          <div>
            <el-button @click="vueCrroper(false)">取 消</el-button>
            <el-button @click="oncropped('base64')">确 定</el-button>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import {VueCropper} from 'vue-cropper';
import axios from "axios";

export default {
  name: "equipment_picture",
  components: {
    VueCropper,
  },
  data() {
    return {
      bdbi_id: '',
      img_tableData: [],
      banner_img: '',
      ruleForm: {
        di_uuid: '',
      },
      //截图功能
      options: {
        img: "",
        outputSize: 1,//	 裁剪生成图片的质量 0.1 - 1
        outputType: 'png | jpeg | jpg',	//裁剪生成图片的格式 jpeg || png || webp
        full: true,//是否输出原图比例的截图,true是不失真
        canMove: true,//图片是否可以移动
        canMoveBox: true,//	截图框能否拖动
        original: false,//图片按照原始比例渲染,true会超出屏幕区域，false是适应屏幕区域
        autoCrop: true,//是否默认生成截图框
        autoCropWidth: 400,
        autoCropHeight: 200,
        fixedBox: true,//固定截图框大小 不允许改变
        enlarge: 1,
        centerBox: true,//截图框是否被限制在图片里面
        fixed: true,//	是否开启截图框宽高固定比例
        fixedNumber: [16, 9],// 截图框的宽高比例 [宽度, 高度]
        maxImgSize: 2000,
        mode: 'contain',//整个图片填充裁剪框 contain , cover, 100px, 100% auto
        high: true,
        infoTrue: true,//true截图框展示真实图片的宽高,false截图框内显示可缩放图片
        title: '图像裁剪',
        details: '选择图片，上传',
      },
      dialogVisible: false,
      imgsrc: '',
    }
  },
  props: {
    fixedNumber: {
      type: Array,
      default: [16, 9]
    },
  },
  created() {
    if(this.fixedNumber){
      this.options.fixedNumber=this.fixedNumber
    }
  },
  // 预览实时缩放
  computed: {
    scaleFun: function () {
      var scale = this.scaleratioXY;
      return `transform:scale(${scale},${scale})`;
    }
  },
  methods: {
    vueCrroper(val){
      this.$emit('vueCrroper',false,val)
    },
    // 预览
    realTime(data) {
      var that = this;
      var dataoption = data;
      this.$refs.cropper.getCropData(data => {
        that.imgsrc = data;
        if (dataoption.w >= 200 || dataoption.h >= 200) {
          if (dataoption.w >= dataoption.h) {
            that.scaleratioXY = 200 / dataoption.w;
          }
          if (dataoption.h > dataoption.w) {
            that.scaleratioXY = 200 / dataoption.h;
          }
        }
      });
    },
    // 选择要上传的图片
    uploadImg(e, num) {
      // 选择要上传的图片
      // this.option.img
      var that = this;
      var file = e.target.files[0];


      if (!/\.(jpg|jpeg|png|JPG|PNG)$/.test(e.target.value)) {
        alert('图片类型必须是.gif,jpeg,jpg,png中的一种');
        return false;
      }
      var reader = new FileReader();
      reader.onload = e => {
        let data;
        if (typeof e.target.result === 'object') {
          // 把Array Buffer转化为blob 如果是base64不需要
          data = window.URL.createObjectURL(new Blob([e.target.result]));
        } else {
          data = e.target.result;
        }
        if (num === 1) {
          that.options.img = data;
          // var dat = {img: data};
          // that.$emit('updateimg', dat);
        } else if (num === 2) {
          this.example2.img = data;
        }
      };
      // 转化为base64
      reader.readAsDataURL(file);
      // 转化为blob
      // reader.readAsArrayBuffer(file)
    },
    // 图片缩放
    changeScale(num) {
      num = num || 1;
      this.$refs.cropper.changeScale(num);
    },
    // 左旋转
    rotateLeft() {
      this.$refs.cropper.rotateLeft();
    },
    // 右旋转
    rotateRight() {
      this.$refs.cropper.rotateRight();
    },
    // 裁剪
    oncropped(type) {
      if (this.options.img === '') {
        this.$message({
          message: '您还没有选择图片',
          type: 'warning'
        });
        return
      }
      this.$refs.reference.value=''
      var that = this;
      if (type === 'blob') {
        this.$refs.cropper.getCropBlob(data => {
          // var img = window.URL.createObjectURL(data);
          // console.info("图片数据2："+data);
        });
      } else {
        this.$refs.cropper.getCropData(data => {
          var dats = {img: data};
          // console.info("图片数据1："+data);
          // that.$emit('getcropperimg', dats);
          if (this.options.img === '') {
            alert('您还没有选择图片');
          } else {
            //调用上传接口
            // this.onclose();
            this.uploadFileToServer(data);
            this.dialogVisible = false;
            this.options.img = '';
            this.imgsrc = '';
          }
        });
      }
    },
    // base64转成bolb对象
    dataURItoBlob(base64Data) {
      let byteString;
      if (base64Data.split(',')[0].indexOf('base64') >= 0) {
        byteString = atob(base64Data.split(',')[1]);
      } else {
        byteString = unescape(base64Data.split(',')[1]);
      }
      let mimeString = base64Data.split(',')[0].split(':')[1].split(';')[0];
      let ia = new Uint8Array(byteString.length);
      for (let i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
      }
      return new Blob([ia], {type: mimeString});
    },
    async uploadFileToServer(data) {
      let img = new Image();
      img.src = data;
      img.onload = async () => {
        // let _data = this.compress(img);
        let blob = this.dataURItoBlob(data);
        let formData = new FormData();
        // formData.append('file', blob, this.options.fileInfo.name);
        formData.append('file', blob, this.customGlobal.getFormatDateTimeString("3") + ".png");
        this.uploadFileRequest(formData);
      };
    },
    async uploadFileRequest(params) {
      let that = this;
      const res = axios({
        method: 'post',
        url: this.customGlobal.serverUrl.orgBaseURI + "fr/postCallPowerFileUpload?userid=" + this.customGlobal.userInfoExt.cui_login_id + "&logic=2",
        data: params,
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        onUploadProgress: function (progressEvent) {
          console.info("progressEvent:" + JSON.stringify(progressEvent));
          // 对原生进度事件的处理
        },

      }).then(function (response) {
        console.info("response:" + JSON.stringify(response));
        if (response.data.status === 'success') {
          console.info("上传成功：" + response.data.rawfilename);
          // let img = "http://192.168.3.62:38080/fr/imgserver/" + response.data.rawfilename;
          let img = that.customGlobal.serverUrl.orgBaseURI + "/fr/imgserver/" + response.data.rawfilename;
          // let img = "http://localhost:38080/fr/imgserver/" + response.data.rawfilename;
          console.info("图片：" + img);
          that.banner_img = img;
          console.info("图片：" + that.banner_img);
          that.vueCrroper(that.banner_img)
        } else {
          console.info("上传失败");
        }
      }).catch(function (error) {
        console.log(error);
      });
    },
    //查询图片
    img_select() {
      let that = this;
      let _jsondata_ = {
        perPageSize: 100,
        currentPage: 1,
        obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
        obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
        obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
        obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
        bdbi_type: '1',
        bdbi_status: '1',
        bdbi_device_id: that.ruleForm.di_uuid,
        bdbi_qr_type:''
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.Advertisement_select,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status === 'success') {
          loading.close();
          that.img_tableData = [];
          that.img_tableData = res.data.rows;
        } else {
          console.info("商品查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '商品查询失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '商品查询失败', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    //删除图片
    delete_img(item) {
      let that = this;
      that.bdbi_id = item.bdbi_id;
      console.log(that.bdbi_id);
      this.$confirm('是否删除图片', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString: true,
        closeOnClickModal: false,
        closeOnPressEscape: false,
      }).then(() => {
        let _jsondata_ = {
          bdbi_id: that.bdbi_id,
          bdbi_cui_id: that.customGlobal.userInfoExt.cui_uuid,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        var s3 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Advertisement_delete,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
        );
        s3.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
            this.$message({
              type: 'success',
              showClose: true,
              duration: 3000,
              dangerouslyUseHTMLString: true,
              message: '<span style="color:black;">图片删除成功</span>'
            });
            that.img_select();
          } else {
            console.info("图片删除失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({path: '/login'});
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '图片删除出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '图片删除出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消操作'
        });
      });
    }
  }
}
</script>

<style scoped>
.account-plat-formitem /deep/ .el-form-item {
  margin-bottom: 3px !important;
}

.account-plat-card1 /deep/ .el-card__header {
  /*background:#f5f5f5 !important;*/
  background: #f2f3f8 !important;
  /*height: 10px!important;*/
  padding: 10px !important;
}

.account-plat-card2 /deep/ .el-card__body {
  padding-top: 5px !important;
  padding-bottom: 5px !important;
  padding-left: 20px !important;
  padding-right: 20px !important;
}

/*标题*/
.el-cropper .tit {
  text-align: left;
  height: 42px;
  /*padding: 5px 0px;*/
  line-height: 40px;
  background-color: #fff;
  border-bottom: 1px solid lightgray
}

/*功能btn*/
.el-cropper .l-btn {
  display: inline-block;
  outline: none;
  resize: none;
  border: none;
  padding: 5px 20px;
  background: #409EFF;
  color: #fff;
  border: solid 1px #409EFF;
  border-radius: 3px;
  font-size: 16px;
  margin-left: 1.3vw;
}

.el-cropper .btn:nth-child(2) {
  margin-left: 10px;
}

/*中盒*/
.el-cropper .cropper-content {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  overflow: hidden;
}

/*详情*/
.el-cropper .detailsclass p {
  text-align: left;
  font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif;
}

/**/
.el-cropper .detailsclass p:first-child {
  text-align: left;
  font-weight: 400;
  color: black;
}

/**/
.el-cropper .detailsclass p:last-child {
  text-align: left;
  font-size: 14px;
  margin-top: -5px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  overflow: hidden;
  font-family: "Arial", "Microsoft YaHei", "\9ED1\4F53", "\5B8B\4F53", sans-serif;
  color: #2c3e50;

}

/**/
.el-cropper .cropper {
  width: 65%;
  height: 400px;
  overflow: hidden;
}

/**/
.el-cropper .show-preview {
  /*flex:1;*/
  width: 230px;
  height: auto;
  text-align: left;
  padding: 0px 0px 0px 20px;
  /*margin-top: 20px;*/
  /* display: flex; */
  /* flex-direction:column; */
  /* align-items:stretch; */
}

.el-cropper .detailsclass {
  width: 200px;
  margin-top: 30px;
}

.el-cropper .preview {
  border: 1px solid #ccc;
  overflow: hidden;
}

.show-preview img {
  transform-origin: 0 0 0
}

.wait {
  width: 200px;
  padding: 0px 0px 15px 0px;
  font-weight: 400;
  color: black;
}

.preview {
  width: 200px;
  height: 200px;
  background: #ddd;
}

.account-plat-dialog /deep/ .el-dialog__body {
  padding: 0px 20px !important;
  color: #606266 !important;
  font-size: 14px !important;
  word-break: break-all !important;
  margin-left: 0px !important;
}

.row {
  display: flex;
  align-items: center;
  width: 100%;
  flex-wrap: wrap;
}

.item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 10%;
  padding: 10px;
  /*padding-top: 10px;*/
  /*padding-bottom: 10px;*/
}

.el-tag:hover {
  cursor: pointer;
}
</style>



