<template>
  <div class="box test-5">
    <el-row :gutter="20" v-for="(item, index) in notApproved.tableMsg.length" :class="{'box_row':index>0}" :key="index">
      <el-col :span="4" v-for="(items, indexs) in notApproved.tableMsg[index]" :key="indexs">
        <el-card :body-style="{ padding: '0px' }" class="box_card" v-if="items.bst_img_path">
          <el-checkbox class="box_checkbox" v-model="items.checkShow" @change='checkboxChange(items)'></el-checkbox>
          <el-image :src="items.bst_img_path" class="image" />
        </el-card>
        <el-card :body-style="{ padding: '0px' }" class="box_card1" v-else>
          <div class="avatar-uploader" @click="emit">
            <i class="el-icon-plus avatar-uploader-icon"></i>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        checkedCities: [],
        msg: {},
        notApproved: {
          currentPage: 1,
          perPageSize: 10,
          total: 0,
          tableMsg: [],
        },
      }
    },
    props: {
      shareMsg: {
        type: Object,
        default: {}
      },
      type: {
        default: '1'
      },
      upload: {
        type: Boolean,
        default: true
      },
    },
    created() {
      this.getData()
    },
    methods: {
      // 多选框勾选事件
      checkboxChange(e) {
        if (this.upload) return
        this.notApproved.tableMsg.forEach(res => {
          res.forEach(res1 => {
            if (res1.bst_uuid == e.bst_uuid) {
              res1.checkShow = true
            }else{
              res1.checkShow = false
            }
          })
        })
        this.msg = e
      },
      preservation(){
        if (this.msg.bst_uuid) {
          this.$emit('preservation',this.msg.bst_uuid,this.msg.bst_img_path)
        }
      },
      /**
       * 查询 图片背景库 /fr/postQuerySharingTemplate
        * @param //bst_type 1、系统模板 2、本地模板
        * @param //bst_bank_center_id
        * @param //bst_province_id
        * @param //bst_city_id
        * @param //bst_county_id
        * @param //bst_station_id
      */
      getData(type) {
        let that = this;
        let _jsondata_ = {
          bst_type: type ? type : this.type,
          bst_bank_center_id: this.shareMsg.bsi_bank_center_id ? this.shareMsg.bsi_bank_center_id : this.shareMsg.organization_uuid,
          bst_province_id: this.shareMsg.bsi_province_id,
          bst_city_id: this.shareMsg.bsi_city_id,
          bst_county_id: this.shareMsg.bsi_county_id,
          bst_station_id: this.shareMsg.bsi_station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postQuerySharingTemplate,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          if (res.data.status == 'success') {
            if (this.upload) {
              let data = {
                bst_img_path: false
              }
              res.data.rows.unshift(data)
            }
            res.data.rows.forEach(i => {
              i.checkShow = false
            });
            that.notApproved.tableMsg = this.formatArray(res.data.rows, 6);
            console.log('---------------------------------------------------------');
            console.log(res.data.rows);
            loading.close();
          } else {
            that.notApproved.tableMsg = {};
            console.info("货道列表查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          that.notApproved.tableMsg = {};
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          that.notApproved.tableMsg = {};
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
      /** 上传图片
       * * @param //bst_type 1、系统模板 2、本地模板
        * @param //bst_bank_center_id
        * @param //bst_province_id
        * @param //bst_city_id
        * @param //bst_county_id
        * @param //bst_station_id
        * @param //bst_img_path 图片路径
        * @param //bst_cui_uuid 创建人ID
       * */
      submitImg(img) {
        let _jsondata_ = {
          bst_type: this.type,
          bst_bank_center_id: this.shareMsg.bsi_bank_center_id ? this.shareMsg.bsi_bank_center_id : this.shareMsg.organization_uuid,
          bst_province_id: this.shareMsg.bsi_province_id,
          bst_city_id: this.shareMsg.bsi_city_id,
          bst_county_id: this.shareMsg.bsi_county_id,
          bst_station_id: this.shareMsg.bsi_station_id,
          bst_img_path: img,
          bst_cui_uuid: this.customGlobal.userInfoExt.cui_uuid,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        var d2 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postCreateSharingTemplate,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        d2.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
          } else {
            console.info("上传失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          this.$alert(JSON.stringify(error.message), '上传出错!', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          this.$alert(JSON.stringify(res.message), '上传出错', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        });
      },
      /** 删除图片
        * @param //bst_type 1、系统模板 2、本地模板
        * @param //bst_uuid 模板ID,多个ID用分号分隔
        * @param //bst_bank_center_id
        * @param //bst_province_id
        * @param //bst_city_id
        * @param //bst_county_id
        * @param //bst_station_id
        * @param //bst_img_path 图片路径,多个路径用分号分隔
        * @param //bst_cui_uuid 删除人ID
       * */
      deleteImg() {
        let bst_uuid = ''
        let img = ''
        this.notApproved.tableMsg.forEach(res => {
          res.forEach(res1 => {
            if (res1.checkShow) {
              if (!bst_uuid) {
                bst_uuid += res1.bst_uuid
                img += res1.bst_img_path
              } else {
                bst_uuid += ";" + res1.bst_uuid
                img += ";" + res1.bst_img_path
              }
            }
          })
        })

        let _jsondata_ = {
          bst_type: this.type,
          bst_uuid: bst_uuid,
          bst_bank_center_id: this.shareMsg.bsi_bank_center_id ? this.shareMsg.bsi_bank_center_id : this.shareMsg.organization_uuid,
          bst_province_id: this.shareMsg.bsi_province_id,
          bst_city_id: this.shareMsg.bsi_city_id,
          bst_county_id: this.shareMsg.bsi_county_id,
          bst_station_id: this.shareMsg.bsi_station_id,
          bst_img_path: img,
          bst_cui_uuid: this.customGlobal.userInfoExt.cui_uuid,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        var d2 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postRemoveSharingTemplate,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        d2.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
            this.getData()
          } else {
            console.info("上传失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          this.$alert(JSON.stringify(error.message), '上传出错!', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          this.$alert(JSON.stringify(res.message), '上传出错', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        });
      },
      //数组重组
      formatArray: function (arr, num) {
        var newArray = new Array(Math.ceil(arr.length / num));
        for (var i = 0; i < newArray.length; i++) {
          newArray[i] = [];
        }
        for (var i = 0; i < arr.length; i++) {
          newArray[parseInt(i / num)][i % num] = arr[i];
        }
        return newArray;
      },
      emit() {
        this.$emit('vueCrroper', true)
      }
    },
  }
</script>
<style scoped>
  .box {
    width: 100%;
    height: 500px;
    padding: 0 10px;
  }

  .image {
    width: 100%;
    height: 100%;
  }

  .box_row {
    margin-top: 20px;
  }

  .box_card {
    width: 180px;
    height: 312px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }

  .box_card1 {
    width: 180px;
    height: 312px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    border: none;
    box-shadow: none;
    position: relative;
  }

  .box_checkbox {
    position: absolute;
    top: 8px;
    right: 10px;
    z-index: 100;
    transform: scale(1.2);
  }

  .avatar-uploader {
    width: 178px;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .insitute-card2 /deep/ .el-card__body {
    padding: 0px !important;
  }
</style>


