<template>
  <div class="box" style="position: relative;">
    <div style="position: absolute;top: -50px; right: 24px;" v-if="doi_id_show!=='#2'">
      <el-button @click='postResetDeviceSoltNumberStatusClick' type="primary">重置所有货道为正常</el-button>
    </div>
    <div class="test-5" style="width: 95%;height:400px;margin: 0 auto;overflow: auto;"
      v-if="notApproved2.tableMsg.graph&&doi_id_show!=='#2'">
      <ul v-for="(items, indexs) in notApproved2.tableMsg.graph.detail" :key="indexs"
        style="display: flex;width: 100%;justify-content: space-between;">
        <li v-for="(item, index) in items.detail" :key="index">
          <!-- {{item}} -->
          <el-card class="card" shadow='none' :style="{'width':item.width+'px','height':item.height+'px'}">
            <div
              style="width: 60%;border-bottom: 1px solid rgb(102, 177, 255);border-right: 1px solid rgb(102, 177, 255);">
              <span>货道号：{{item.id}}</span>
            </div>
            <div v-if="item.tableShow">
              <div
                style="margin-top: 50px; width: 100%;height: 120px;display: flex;align-items: center;justify-content: space-between;flex-direction: column;">
                <div>商品名称：{{item.tabledata.mbgi_goods_name}}</div>
                <div>货道已占用</div>
                <el-button v-if="item.tabledata.mbgi_uuid==obj.mbgi_uuid" type="danger" size="mini"
                  @click="Disassociate_cargo_lanes_update(item)">取消关联</el-button>
                <el-button v-if="item.tabledata.mbgi_uuid==obj.mbgi_uuid&&item.checked==false" style="margin: 0 0;"
                  type="danger" size="mini" @click="item.checked=true">修改库存</el-button>
                <el-button v-if="item.tabledata.mbgi_uuid==obj.mbgi_uuid&&item.checked==true" style="margin: 0 0;"
                  type="danger" size="mini" @click="Disassociate_cargo_lanes_update1(item)">保存</el-button>
                <div v-if="item.tabledata.mbgi_uuid!==obj.mbgi_uuid"></div>
              </div>
              <div style="position: absolute;bottom: 0px;width: 100%;line-height: 40px;">
                <div style="width: 100%;height: 30px;">货道存量</div>
                <el-input class="elInput" v-if="item.checked" v-model='item.tabledata.C' @input='inputChange1(item)'
                  placeholder="请输入库存数量" @change="inputChanged"></el-input>
                <el-input class="elInput" v-else disabled placeholder="请输入库存数量" :value='item.tabledata.C'
                  @change="inputChanged"></el-input>
                <div style="width: 100%;height: 40px;border-top: 1px solid rgb(102, 177, 255);">
                  货道状态：{{item.status?'正常':'故障'}}</div>
              </div>
            </div>
            <div v-else>
              <div style="width: 100%;height: 200px;display: flex;align-items: center;justify-content: center;">
                <el-checkbox v-model="item.checked" @change='checkboxChange(item)'>关联货道</el-checkbox>
              </div>
              <div style="position: absolute;bottom: 0px;width: 100%;line-height: 40px;">
                <div style="width: 100%;height: 30px;">货道存量</div>
                <el-input class="elInput" v-if="item.checked" v-model="item.value" @input='inputChange(item)'
                  placeholder="请输入库存数量" @change="inputChanged"></el-input>
                <el-input class="elInput" v-else disabled placeholder="请输入库存数量" @change="inputChanged"></el-input>
                <div style="width: 100%;height: 40px;border-top: 1px solid rgb(102, 177, 255);">
                  货道状态：{{item.status?'正常':'故障'}}</div>
              </div>
            </div>
          </el-card>
        </li>
      </ul>
    </div>
    <div class="test-5" style="width: 95%;height:400px;margin: 0 auto;overflow: auto;"
      v-if="notApproved2.tableMsg.graph&&doi_id_show=='#2'">
      <ul v-for="(items, indexs) in notApproved2.tableMsg.graph.detail" :key="indexs"
        style="display: flex;width: 100%;justify-content: space-between;">
        <li v-for="(item, index) in items.detail" :key="index">
          <!-- {{item}} -->
          <el-card class="card" shadow='none'
            :style="{'width':(item.width-item.width*0.2)+'px','height':(item.height-item.height*0.2)+'px'}">
            <div
              style="width: 60%;border-bottom: 1px solid rgb(102, 177, 255);border-right: 1px solid rgb(102, 177, 255);">
              <span>货道号：{{item.id}}</span>
            </div>
            <div style="width: 100%;height: 100px;display: flex;align-items: center;justify-content: center;">
              <el-checkbox disabled>关联货道</el-checkbox>
            </div>
            <div style="position: absolute;bottom: 0px;width: 100%;line-height: 40px;">
              <div style="width: 100%;height: 30px;">货道存量</div>
              <el-input style="width: 100%;height: 40px;border-top: 1px solid rgb(102, 177, 255);"
                @change="inputChanged" v-model="input" placeholder="请输入库存数量"></el-input>
              <div style="width: 100%;height: 40px;border-top: 1px solid rgb(102, 177, 255);">
                货道状态：{{item.status?'正常':'故障'}}</div>
            </div>
          </el-card>
        </li>
      </ul>
    </div>
  </div>

</template>

<script>
  export default {
    data() {
      return {
        filterMsg: [],
        input: '',
        show: false,
        checked: 0,
        CargoWay: '',
        checkedCities: [],
        tabledata: [],
        notApproved: {
          currentPage: 1,
          perPageSize: 10,
          total: 0,
          tableMsg: {},
        },
        notApproved2: {
          currentPage: 1,
          perPageSize: 10,
          total: 0,
          tableMsg: {},
        },
      }
    },
    props: ["doi_id", 'msg', 'doi_id_show', 'Id', 'obj'],
    created() {
      this.getData()
      if (this.msg) {
        this.notApproved.tableMsg.graph = JSON.parse(this.msg)
        this.notApproved2 = this.notApproved
      }
      console.log(this.obj.mbgi_uuid, "-------------");
    },
    watch: {
      msg() {
        this.notApproved.tableMsg.graph = JSON.parse(this.msg)
        this.notApproved2 = this.notApproved
      }
    },
    methods: {
      inputChanged(e) {
        console.log('res==', e, '---------------------------------------------------------');
      },
      checkboxChange(e) {
        if (e.checked) {
          this.checkedCities.push(e)
        } else {
          this.checkedCities = this.checkedCities.filter(res => {
            return res.checked == true
          })
        }
        console.log(this.checkedCities);
        if (this.filterMsg.length > 0) {
          let value = 0
          this.filterMsg.forEach(res => {
            return value += res.a * 1
          })
          this.$emit('checkedChange', this.checkedCities, value)
        } else {
          this.$emit('checkedChange', this.checkedCities, false)
        }
      },
      inputChange(e) {
        if (this.filterMsg.length > 0) {
          let value = 0
          this.filterMsg.forEach(res => {
            console.log(value);
            return value += res.a * 1
          })
          this.$emit('checkedChange', this.checkedCities, value)
        } else {
          this.$emit('checkedChange', this.checkedCities, false)
        }
      },
      inputChange1(e) {
        console.log(e);
      },
      /**
       * 获取设备货道图 /fr/postGetDeviceGraphInfo
       * @param //doi_id 设备ID
       * @return //graph 货道图
      */
      getData() {
        // return
        let that = this;
        let _jsondata_ = {
          doi_id: this.doi_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postGetDeviceGraphInfo,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          if (res.data.status == 'success') {
            that.notApproved.tableMsg = JSON.parse(res.data.graph);
            if (typeof that.notApproved.tableMsg.graph !== 'object') {
              that.notApproved.tableMsg.graph = JSON.parse(that.notApproved.tableMsg.graph);
            }
            this.getData1()
            loading.close();
          } else {
            that.notApproved.tableMsg = {};
            console.info("货道列表查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          that.notApproved.tableMsg = {};
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          that.notApproved.tableMsg = {};
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
      /**
       * 获取设备货道状态 /fr/Faulty_track_number_select
       * @param //doi_id 设备ID
       * @return //graph 货道图
      */
      getData1() {
        // return
        let that = this;
        let _jsondata_ = {
          perPageSize: 1000,
          currentPage: 1,
          mbds_device_uuid: this.doi_id,//设备ID
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Faulty_track_number_select,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          if (res.data.status == 'success') {
            if (res.data.rows.length > 0) {
              res.data.rows.forEach(res1 => {
                console.log(res1);
                this.notApproved.tableMsg.graph.detail.forEach(res2 => {
                  res2.detail.forEach(res3 => {
                    if (res3.id == res1.mbds_number) {
                      res3.status = 0
                    }
                  })
                });
              })
            }
            this.Associated_cargo_lane_information_selectClick()
            loading.close();
          } else {
            that.notApproved.tableMsg = {};
            console.info("货道列表查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          that.notApproved.tableMsg = {};
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          that.notApproved.tableMsg = {};
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },

      /**
        * @description: 查询关联货道的货道信息（货道号和商品）
      */
      Associated_cargo_lane_information_selectClick() {
        /*
         * @param: perPageSize  分页参数：每页条数
        * @param: currentPage  分页参数：当前页
        * @param: obj_bank_center_id  行系ID
        * @param: obj_province_id  省行ID
        * @param: obj_city_id  市行ID
        * @param: obj_county_id  县区行ID
        * @param: obj_station_id  网点ID
        * @param: mdgi_device_uuid  设备ID
        */
        let that = this;
        let _jsondata_ = {
          perPageSize: 1000,
          currentPage: 1,
          obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
          obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
          obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
          obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
          obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
          mdgi_device_uuid: this.doi_id,//设备ID
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Associated_cargo_lane_information_select,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          if (res.data.status == 'success') {
            console.log(res.data.rows, "------------------------");
            if (res.data.rows.length > 0) {
              // mbgi_uuid
              this.filterMsg = res.data.rows.filter(res => {
                return res.mbgi_uuid == this.Id && res.gbs_number
              })
              res.data.rows.forEach(res1 => {
                console.log(res1);
                this.notApproved.tableMsg.graph.detail.forEach(res2 => {
                  res2.detail.forEach(res3 => {
                    if (res3.id == res1.gbs_number) {
                      res3.tabledata = res1
                      res3.tableShow = true
                    }
                  })
                });
                this.notApproved2.tableMsg = this.notApproved.tableMsg
              })
            } else {
              this.notApproved2.tableMsg = this.notApproved.tableMsg
            }
            console.log(this.notApproved.tableMsg.graph.detail, "000000000000000000000000");
            loading.close();
          } else {
            console.info("货道列表查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          this.$alert(JSON.stringify(error.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          this.$alert(JSON.stringify(res.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },

      /**
        * @description: q取消关联
      */
      Disassociate_cargo_lanes_update(e) {
        /*
        * @param: perPageSize  分页参数：每页条数
        * @param: currentPage  分页参数：当前页
        * @param: obj_bank_center_id  行系ID
        * @param: obj_province_id  省行ID
        * @param: obj_city_id  市行ID
        * @param: obj_county_id  县区行ID
        * @param: obj_station_id  网点ID
        * @param: mdgi_device_uuid  设备ID
        * @param: mdgi_number  货道号
        * @param: mdgi_goods_type_uuid  商品分类ID
        */
        let that = this;
        let _jsondata_ = {
          perPageSize: 1000,
          currentPage: 1,
          obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
          obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
          obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
          obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
          obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
          mdgi_device_uuid: this.doi_id,//设备ID
          mdgi_number: e.id,//货道号
          mdgi_goods_type_uuid: e.tabledata.mdgi_goods_type_uuid,//商品分类ID
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Disassociate_cargo_lanes_update,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          if (res.data.status == 'success') {
            this.$emit('CommodityRackClick');
            this.getData()
            this.$message({
              message: res.data.msg,
              type: 'success'
            });
            loading.close();
          } else {
            console.info("货道列表查询失败");
            this.$message({
              message: res.data.msg,
              type: 'success'
            });
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          this.$alert(JSON.stringify(error.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          this.$alert(JSON.stringify(res.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
      /**
        * @description: 修改库存
      */
      Disassociate_cargo_lanes_update1(e) {
        /*
          * @description: 修改关联货道的某货道的商品库存
          * @param: mdgi_device_uuid  设备ID
          * @param: mdgi_goods_type_uuid  商品分类ID
          * @param: mdgi_number  商品分类ID
          * @param: modified_quantity  传入修改的库存数量
        */
        let that = this;
        let _jsondata_ = {
          perPageSize: 1000,
          currentPage: 1,
          modified_quantity: e.tabledata.C,
          mdgi_device_uuid: this.doi_id,//设备ID
          mdgi_number: e.id,//货道号
          mdgi_goods_type_uuid: e.tabledata.mdgi_goods_type_uuid,//商品分类ID
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Modify_aisle_inventory_update,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          if (res.data.status == 'success') {
            this.getData()
            this.$emit('CommodityRackClick');
            this.$message({
              message: '库存修改成功',
              type: 'success'
            });
            loading.close();
          } else {
            console.info("库存修改失败");
            this.$message({
              message: res.data.msg,
              type: 'error'
            });
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          this.$alert(JSON.stringify(error.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          this.$alert(JSON.stringify(res.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
      /**
        * @description: 重置货道状态
      */
      postResetDeviceSoltNumberStatusClick(e) {
        /*
        * @param: mbds_device_uuid  设备ID
        */
        let that = this;
        let _jsondata_ = {
          mbds_device_uuid: this.doi_id,//设备ID
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postResetDeviceSoltNumberStatus,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          if (res.data.status == 'success') {
            this.getData()
            this.$message({
              message: res.data.msg,
              type: 'success'
            });
            loading.close();
          } else {
            console.info("货道列表查询失败");
            this.$message({
              message: res.data.msg,
              type: 'success'
            });
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          this.$alert(JSON.stringify(error.message), '货道列表重置失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          this.$alert(JSON.stringify(res.message), '货道列表重置失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },


    },
  }
</script>
<style scoped>
  li {
    list-style: none;
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
  }

  ul {
    padding: 0 10px 0 0;
    margin: 0;
  }

  .test-5 {
    overflow: auto;
    /* overflow-x: scroll; */
    /* table-layout: fixed; */
    /* word-wrap: break-word;
    word-break: break-all; */
  }

  .test-5::-webkit-scrollbar {
    /*滚动条整体样式*/
    width: 5px;
    /*高宽分别对应横竖滚动条的尺寸*/
    height: 1px;
  }

  .test-5::-webkit-scrollbar-thumb {
    /*滚动条里面小方块*/
    border-radius: 10px;
    background-color: skyblue;
    background-image: -webkit-linear-gradient(45deg,
        rgba(255, 255, 255, 0.2) 25%,
        transparent 25%,
        transparent 50%,
        rgba(255, 255, 255, 0.2) 50%,
        rgba(255, 255, 255, 0.2) 75%,
        transparent 75%,
        transparent);
  }

  .test-5::-webkit-scrollbar-track {
    /*滚动条里面轨道*/
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    /* background   : ; */
    border-radius: 10px;
  }

  .text {
    font-size: 14px;
  }


  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .card {
    border: 1px solid rgb(102, 177, 255);
    position: relative;
  }

  .box /deep/ .el-card__body {
    padding: 0px !important;
  }

  .box /deep/ input {
    padding: 0px;
    text-align: center;
    outline: none;
    border: none;
  }

  .box /deep/ .el-radio__input {
    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: middle;
  }

  .box /deep/ .el-radio__inner {
    border-radius: 2px !important;
    cursor: pointer;
    display: inline-block;
    position: relative;
    border: 1px solid #dcdfe6;
    border-radius: 2px;
    box-sizing: border-box;
    width: 14px;
    height: 14px;
    z-index: 1;
    transition: border-color .25s cubic-bezier(.71, -.46, .29, 1.46), background-color .25s cubic-bezier(.71, -.46, .29, 1.46);
  }

  .box /deep/ .el-radio__input.is-checked .el-radio__inner::after {
    transform: rotate(45deg) scaleY(1) !important;
  }

  .box /deep/ .el-radio__inner::after {
    background: none;
    box-sizing: content-box;
    content: "";
    border: 1px solid white;
    border-top: transparent;
    border-left: transparent;
    height: 7px;
    left: 4px;
    position: absolute;
    top: 1px;
    transform: rotate(45deg) scaleY(0);
    width: 3px;
    transition: transform 0s ease-in 0s;
    transform-origin: center;
  }

  .elInput {
    width: 100%;
    height: 40px;
    border-top: 1px solid rgb(102, 177, 255);
  }
</style>


